.userAvatar {
  width: 75px;
  height: 75px;
  object-fit: cover;
  border-radius: 50%;
}

.leftSide {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.user {
  display: flex;
  margin: 18px 10px;
}

.aboutUser {
  border: 0.5px solid rgba(145, 145, 145, 0.26);
  margin-left: 13px;
  box-shadow: 0px 0px 15px 6px rgb(34 34 34 / 25%);
  padding: 8px 18px;
  border-radius: 10px;
  flex: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.userName {
  font-size: 18px;
}
.deleteFriend {
  background-color: rgba(220, 20, 60, 0.753);
}

.addFriend {
  background-color: rgba(57, 117, 247, 0.699);
}
.btn {
  border: none;
  color: #fff;
  padding: 5px 8px;
  font-size: 10px;
  font-weight: 800;
  border-radius: 17px;
  margin-top: 8px;
  cursor: pointer;
}

button:disabled,
button[disabled] {
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
}
